/**
 * Gestionnaire de visualisation des items.
 * ImplÃ©mentation en onglets principaux + slots Ã  gauche et Ã  droite + fenÃªtres flottantes.
 */
export class ItemViewerMulti implements IItemViewer {

	reg: IReg<IWspUiEnv>;

	fallbackOpenSrcRef?: (srcRef: srcRef, longdesc: JSrcFields, silent?: boolean) => Promise<true | null>;

	/**
	 * Ensemble des sources actuellement affichÃ©es par cet itemViewer
	 * indexÃ©es par son srcUri.
	 */
	//srcsByUri: Map<string, SrcHolder> = new Map();

	/**
	 * Ensemble des sources actuellement affichÃ©es par cet itemViewer
	 * indexÃ©es par son srcId.
	 */
	//srcsById: Map<string, SrcHolder>;

	/** Nombre d'entrÃ©es dans la liste chainÃ©e des accÃ¨s Ã  SrcHolder. */
	//accessCount = 0;

	/** Cache des registres<IItemTypeUiEnv> indexÃ©s par l'itModel. */
	itemTypesRegs: Map<string, IReg<IItemTypeUiEnv>> = new Map();

	/** Onglets principaux. */
	mainTabs: SrcTabs;

	/** Onglets latÃ©raux gauche. */
	leftTabs: SrcTabs;

	/** Onglets latÃ©raux droit. */
	rightTabs: SrcTabs;

	/** FenÃªtres flottantes ouvertes. */
	floatings: PopupFloating[];

	protected onWspUriChange: (msg: JWspUriChangeMsg, from: WspsLivePlace | 'local' | 'server') => void;


	async initViewer(reg: IReg<IWspUiEnv>, mainViewParent: HTMLElement, lastDatas: JLastDatas, select: srcRef): Promise<any> {
		if (this.reg) console.error("ItemViewerMulti already inited");
		this.reg = reg;
		const infoBroker = reg.env.infoBroker;
		if (infoBroker) infoBroker.addConsumer(this);
		if (lastDatas) {
			const mainLD = (lastDatas as JLDItemViewerMulti).main;
			if (mainLD && mainLD.tabs) {
				this._initUi(mainViewParent, await this._buildAreas(mainLD, select, mainViewParent), mainLD);
			} else if (select) {
				this._initUi(mainViewParent, await this._buildAreas(null, select, mainViewParent));
			} else {
				this._initUi(mainViewParent);
			}
			const leftLD = (lastDatas as JLDItemViewerMulti).left;
			if (leftLD && leftLD.tabs) {
				this._initLeftTabs(await this._buildAreas(leftLD, null, mainViewParent), leftLD);
			}
			const rightLD = (lastDatas as JLDItemViewerMulti).right;
			if (rightLD && rightLD.tabs) {
				this._initRightTabs(await this._buildAreas(rightLD, null, mainViewParent), rightLD);
			}
		} else if (select) {
			this._initUi(mainViewParent, await this._buildAreas(null, select, mainViewParent));
		} else {
			this._initUi(mainViewParent);
		}
	}

	protected _initUi(mainViewParent: HTMLElement, mainAreas?: IArea<IItemViewer, SrcMain>[], mainLD?: JLDItemViewerMultiTabs) {
		const root = mainViewParent.appendChild(<wsp-itemviewer-multi/>).attachShadow(DOMSH.SHADOWDOM_INIT);
		this.reg.installSkin("webzone:item", root);
		this.reg.installSkin("wsp-itemviewer-multi", root);
		this.mainTabs = root.appendChild(new SrcTabs().initialize({
			reg: this.reg,
			areasContext: this,
			areas: mainAreas,
			lastDatasKey: 'main',
			lastDatas: mainLD,
			backgroundPanels: this.reg.getSvc<(tabs: Tabs<ItemViewerMulti>) => Promise<HTMLElement>>("ItemViewerMulti.backgroundPanels")
		}));
		this.onWspUriChange = this._onWspUriChange.bind(this);
		this.reg.env.place.eventsMgr.on("wspUriChange", this.onWspUriChange);
	}

	protected _initLeftTabs(leftAreas?: IArea<IItemViewer, SrcMain>[], leftLD?: JLDItemViewerMultiTabs) {
		if (this.leftTabs) return;
		const mainTabs = this.mainTabs;
		this.leftTabs = mainTabs.parentNode.insertBefore(new SrcTabs().initialize({
			reg: this.reg,
			areasContext: this,
			areas: leftAreas,
			lastDatasKey: 'left',
			lastDatas: leftLD
		}), mainTabs);
		mainTabs.parentNode.insertBefore(<Resizer c-orient="row"/>, mainTabs);
		if (!this.leftTabs._tabs.firstElementChild) this.hideTabs(this.leftTabs);
	}

	protected _initRightTabs(rightAreas?: IArea<IItemViewer, SrcMain>[], rightLD?: JLDItemViewerMultiTabs) {
		if (this.rightTabs) return;
		const mainTabs = this.mainTabs;
		mainTabs.parentNode.appendChild(<Resizer c-orient="row"/>);
		this.rightTabs = mainTabs.parentNode.appendChild(new SrcTabs().initialize({
			reg: this.reg,
			areasContext: this,
			areas: rightAreas,
			lastDatasKey: 'right',
			lastDatas: rightLD
		}));
		if (!this.rightTabs._tabs.firstElementChild) this.hideTabs(this.rightTabs);
	}

	protected async _buildAreas(ldTabs: JLDItemViewerMultiTabs, select: srcRef, uiCtx: HTMLElement): Promise<IArea<IItemViewer, SrcMain>[]> {
		let srcRefs: srcRef[];
		if (select) {
			if (ldTabs) {
				//indÃ©pendamment des lastDatas, on impose la sel d'un item via les JWspAppDef (queryString de l'URL)
				ldTabs.tab = select;
				let selectFound: boolean;
				srcRefs = ldTabs.tabs.map((t) => {
					if (select === t.srcRef) selectFound = true;
					return t.srcRef;
				});
				if (!selectFound) {
					//cet item sÃ©lectionnÃ© par les JWspAppDef n'est pas prÃ©sent dans les onglets Ã  rÃ©afficher.
					//On ajoute un onglet pour afficher 'select'.
					srcRefs.push(select);
					ldTabs.tabs.push({srcRef: select});
				}
			} else {
				//pas de lastDatas, mais un select
				ldTabs = {tabs: [{srcRef: select}]};
				srcRefs = [select];
			}
		} else {
			srcRefs = ldTabs.tabs.map(t => t.srcRef).filter((srcRef) => !SRC.isNewSrcUri(srcRef));
		}
		const srcs = await WSP.fetchShortDescs(this.reg.env.wsp, uiCtx, ...srcRefs);
		const results: SrcArea[] = [];
		let k = 0;
		for (let i = 0, s = ldTabs.tabs.length; i < s; i++) {
			const ldTab = ldTabs.tabs[i];
			const sd = SRC.isNewSrcUri(ldTab.srcRef) ? {srcUri: ldTab.srcRef, itModel: SRC.extractItModelFromNewSrcUri(ldTab.srcRef), srcSt: 1} : srcs[k++];
			if (sd.srcSt > 0 && sd.itModel != null) {
				results.push(new SrcArea(this, sd, null, ldTab).setPinned(ldTab.pin).setShowTime(i));
			}
		}
		return results;
	}

	openSrcRef(srcRef: srcRef, silent?: boolean, lastDatas?: JLastDatas): Promise<boolean | null> {
		return this.openSrcRefInSrcTabs(this.mainTabs, srcRef, silent, lastDatas);
	}

	onInfo(info: IInfo): void {
		if (info instanceof InfoFocusItem) {
			if (info instanceof InfoFocusNodeInItem) {
				this.focusNodeInItem(info);
			} else {
				this.focusItem(info);
			}
			info.handled = true;
		} else if (info instanceof InfoReqCurrentItem) {
			if (this.mainTabs) {
				const tab = this.mainTabs.selectedTab;
				if (tab && tab.view instanceof SrcMain) {
					const longDesc = tab.view.reg.env.longDesc;
					if (longDesc) {
						info.srcUri = longDesc.srcUri;
						info.shortDesc = longDesc;
					}
				}
			}
		} else if (info instanceof InfoSrcUriMoved) {
			this.onSrcMoved(info);
		}
	}

	focusItem(info: InfoFocusItem) {
		this.openSrcRef(info.srcRef, false, info.lastDatas);
	}

	async focusNodeInItem(info: InfoFocusNodeInItem) {
		if (this.isSrcRefShown(this.mainTabs, info.srcRef)) return; //item dÃ©jÃ  chargÃ© et affichÃ© en 1er plan, il va gÃ©rer la partie "inItem".
		if (await this.openSrcRef(info.srcRef)) {
			//On redispatch la demande une fois l'item chargÃ©.
			const tab = this.mainTabs.selectedTab;
			if (tab.view instanceof BaseElementAsync) await tab.view.initializedAsync;
			this.reg.env.infoBroker.dispatchInfo(new InfoFocusNodeInItem(info.xpath, info.srcRef), this);
		}
	}

	/**
	 * Si l'ancienne uri est prÃ©sente et la nouvelle uri est absente,
	 * on remplace l'onglet avec la nouvelle uri.
	 */
	async onSrcMoved(info: InfoSrcUriMoved) {
		const tab = !this.mainTabs.getTabBySrcRef(info.newSrcUri) ? this.mainTabs.getTabBySrcRef(info.oldSrcUri) : null;
		const tabLeft = !this.leftTabs?.getTabBySrcRef(info.newSrcUri) ? this.leftTabs?.getTabBySrcRef(info.oldSrcUri) : null;
		const tabRight = !this.leftTabs?.getTabBySrcRef(info.newSrcUri) ? this.rightTabs?.getTabBySrcRef(info.oldSrcUri) : null;
		if (tab || tabLeft || tabRight) {
			const wsp = this.reg.env.wsp;
			const longDesc = await wsp.fetchLongDesc(info.newSrcUri);
			if (longDesc.srcSt < 0 || longDesc.itModel == null) {
				// tab?.closeTabView(true);
				// tabLeft?.closeTabView(true);
				// tabRight?.closeTabView(true);
				return false;
			}
			if (tab && tab.isConnected) await this.mainTabs.replaceTab(tab, new SrcArea(this, longDesc, longDesc));
			if (tabLeft && tabLeft.isConnected) await this.leftTabs.replaceTab(tabLeft, new SrcArea(this, longDesc, longDesc));
			if (tabRight && tabRight.isConnected) await this.leftTabs.replaceTab(tabRight, new SrcArea(this, longDesc, longDesc));
		}
		return true;
	}

	openSrcRefInTabs(tabPos: 'left' | 'main' | 'right', srcRef: srcRef, silent?: boolean): Promise<boolean> {
		switch (tabPos) {
		case 'main':
			return this.openSrcRefInSrcTabs(this.mainTabs, srcRef, silent);
		case 'left':
			if (!this.leftTabs) this._initLeftTabs();
			this.showTabs(this.leftTabs);
			return this.openSrcRefInSrcTabs(this.leftTabs, srcRef, silent);
		case 'right':
			if (!this.rightTabs) this._initRightTabs();
			this.showTabs(this.rightTabs);
			return this.openSrcRefInSrcTabs(this.rightTabs, srcRef, silent);
		}
		throw Error(tabPos);
	}

	isSrcRefShown(tabs: SrcTabs, srcRef: srcRef): boolean {
		const tab = tabs.getTabByCode(srcRef);
		if (tab) return tabs.selectedTab === tab;
		return false;
	}

	async openSrcRefInSrcTabs(tabs: SrcTabs, srcRef: srcRef, silent?: boolean, lastDatas?: JLastDatas): Promise<boolean | null> {
		try {
			const tab = tabs.getTabByCode(srcRef);
			if (tab) return tabs.selectTab(tab);
			const wsp = this.reg.env.wsp;
			const longDesc =
				SRC.isNewSrcUri(srcRef) ?
					//Fake longDesc si contenu en crÃ©ation
					{srcUri: srcRef, itModel: SRC.extractItModelFromNewSrcUri(srcRef), srcSt: 1, srcRoles: this.reg.env.securityCtx.srcRoles} :
					//Fetch longDesc
					await wsp.fetchLongDesc(srcRef);
			if (longDesc.srcSt < 0 || longDesc.itModel == null) {
				if (this.fallbackOpenSrcRef) {
					return this.fallbackOpenSrcRef(srcRef, longDesc, silent);
				} else {
					if (!silent) POPUP.showNotifInfo("Ce contenu n'est plus disponible.", tabs);
					return null;
				}
			}
			await tabs.addTab(new SrcArea(this, longDesc, longDesc, lastDatas), {select: true});
			return true;
		} catch (e) {
			console.trace(e);
			return null;
		}
	}

	getRegItemType(itModel: string) {
		const wsp = this.reg.env.wsp;
		let reg = this.itemTypesRegs.get(itModel);
		if (!reg) {
			const itemType = wsp.wspMetaUi.getItemType(itModel);
			reg = REG.createSubRegMixed(this.reg, itemType.reg);
			this.itemTypesRegs.set(itModel, reg);
		}
		return reg;
	}

	onViewShown() {
		//this.app.reg.env.wsp.
		VIEWS.onViewShown(this.mainTabs);
		if (this.leftTabs) VIEWS.onViewShown(this.leftTabs);
		if (this.rightTabs) VIEWS.onViewShown(this.rightTabs);
		if (this.floatings) throw Error("TODO floatings...");
	}

	onViewHidden(closed?: boolean) {
		if (closed) {
			this.reg.env.place.eventsMgr.removeListener("wspUriChange", this.onWspUriChange);
			VIEWS.onViewHidden(this.mainTabs, true);
			if (this.leftTabs) VIEWS.onViewHidden(this.leftTabs, true);
			if (this.rightTabs) VIEWS.onViewHidden(this.rightTabs, true);
			if (this.floatings) throw Error("TODO floatings...");
		} else {
			VIEWS.onViewHidden(this.mainTabs, false);
			if (this.leftTabs) VIEWS.onViewHidden(this.leftTabs, false);
			if (this.rightTabs) VIEWS.onViewHidden(this.rightTabs, false);
			if (this.floatings) throw Error("TODO floatings...");
		}
	}

	buildLastDatas(parentLastDatas: JLDItemViewerMulti): void {
		//TODO buildLastDatas floatings.
		//Les lastDatas des onglets sont buildÃ©s naturellement par l'arbo DOM.
	}

	protected _onWspUriChange(msg: JWspUriChangeMsg, from: WspsLivePlace | 'local' | 'server') {
		if (msg.type === EWspChangesEvts.r) {
			//Traitement du remove
			const type = ITEM.getSrcUriType(msg.srcUri);
			if (type === 'item') {
				//Suppr de l'item
				const srcRef = SRC.srcRef(msg);
				this.mainTabs.closeItem(srcRef);
				if (this.leftTabs) this.leftTabs.closeItem(srcRef);
				if (this.rightTabs) this.rightTabs.closeItem(srcRef);
			} else if (type === 'space') {
				//Suppr d'un espace ancÃªtre de l'item
				this.mainTabs.closeSpace(msg.srcUri);
				if (this.leftTabs) this.leftTabs.closeSpace(msg.srcUri);
				if (this.rightTabs) this.rightTabs.closeSpace(msg.srcUri);
			}
		} else if (msg.type === EWspChangesEvts.u) {
			//Traitement de l'update
			const srcRef = SRC.srcRef(msg);
			const tab = this.getOneTabForSrcRef(srcRef);
			if (tab && tab.view) {
				//item construit, faut dÃ©tecter si l'itModel n'a pas Ã©tÃ© modifiÃ©
				const oldItModel = (tab.area as SrcArea).shortDesc.itModel;
				this.reg.env.wsp.fetchLongDesc(srcRef, this.mainTabs).then((ld: JSrcFields) => {
					this.refreshSrc(ld, ld, oldItModel !== ld.itModel);
				});
			} else if (tab) {
				//item dans les onglets, mais non construit
				this.reg.env.wsp.fetchShortDesc(srcRef, this.mainTabs).then((sd: JSrcFields) => {
					this.refreshSrc(sd);
				});
			}
		} else if (msg.type === EWspChangesEvts.s) {
			const srcRef = SRC.srcRef(msg);
			const tab = this.getOneTabForSrcRef(srcRef);
			if (tab) {
				const area = tab.area as SrcArea;
				if (area.longDesc) {
					area.longDesc.itSt = msg.itSt;
					this.refreshSrc(area.longDesc, area.longDesc);
				} else {
					area.shortDesc.itSt = msg.itSt;
					this.refreshSrc(area.shortDesc);
				}
			}
		}
	}

	/** Retourne le 1er tab pour ce srcRef avec view construite, ou Ã  dÃ©faut, sans view construite. */
	getOneTabForSrcRef(srcRef: srcRef): SrcTab {
		let tab = this.mainTabs.getTabBySrcRef(srcRef);
		if ((!tab || !tab.view) && this.leftTabs) tab = this.leftTabs.getTabBySrcRef(srcRef);
		if ((!tab || !tab.view) && this.rightTabs) tab = this.rightTabs.getTabBySrcRef(srcRef);
		return tab;
	}

	refreshSrc(shortDesc: JSrcFields, longDesc?: JSrcFields, rebuild?: boolean) {
		const srcRef = SRC.srcRef(shortDesc);
		let tab = this.mainTabs.getTabBySrcRef(srcRef);
		if (tab) rebuild ? tab.rebuildItemView(this, longDesc) : tab.updateSrc(shortDesc, longDesc);
		if (this.leftTabs) {
			tab = this.leftTabs.getTabBySrcRef(srcRef);
			if (tab) rebuild ? tab.rebuildItemView(this, longDesc) : tab.updateSrc(shortDesc, longDesc);
		}
		if (this.rightTabs) {
			tab = this.rightTabs.getTabBySrcRef(srcRef);
			if (tab) rebuild ? tab.rebuildItemView(this, longDesc) : tab.updateSrc(shortDesc, longDesc);
		}
	}

	/** Masque un SrcTabs secondaire. */
	hideTabs(tabs: SrcTabs) {
		if (DOM.setHidden(tabs, true)) {
			if (tabs.tabsPos === 'left') (tabs.nextElementSibling as Resizer).hidden = true;
			else (tabs.previousElementSibling as Resizer).hidden = true;
		}
	}

	/** VÃ©rifie qu'un SrcTabs secondaire est visible. */
	showTabs(tabs: SrcTabs) {
		if (DOM.setHidden(tabs, false)) {
			if (tabs.tabsPos === 'left') (tabs.nextElementSibling as Resizer).hidden = false;
			else (tabs.previousElementSibling as Resizer).hidden = false;
		}
	}
}


