/**
 * Area d'un ItemMain dans le contexte du ItemViewerMulti.
 * Chaque instance d'area est spÃ©cifique Ã  chaque src : SrcArea implÃ©mente ISrcMainCtx
 * pour servir de context Ã  l'ItemMainArea fournit par l'itemType.
 */
class SrcArea implements IArea<IItemViewer, SrcMain>, ISrcMainCtx {

	reg: IReg<IItemTypeUiEnv>;
	longDesc: JSrcFields;
	shortDesc: JSrcFields;

	pinned: boolean;
	showTime: number;
	lastDatas: JLastDatas;

	protected _id: string;

	/** ItemMainArea issu de l'itemType. */
	protected _subArea: IArea<ISrcMainCtx, SrcMain>;

	constructor(itemViewer: ItemViewerMulti, shortDesc: JSrcFields, longDesc?: JSrcFields, lastDatas?: JLastDatas) {
		this._id = SRC.srcRef(shortDesc);
		this.shortDesc = shortDesc;
		this.longDesc = longDesc;
		if (lastDatas) this.lastDatas = lastDatas;
		this.reg = itemViewer.getRegItemType(shortDesc.itModel);
		this._subArea = this.reg.env.itemType.getSrcMainArea(this);
	}

	setPinned(p: boolean): this {
		this.pinned = p === true;
		return this;
	}

	setShowTime(t: number): this {
		this.showTime = t;
		return this;
	}

	updateSrcAndModel(itemViewer: ItemViewerMulti, ld: JSrcFields) {
		this.shortDesc = ld;
		this.longDesc = ld;
		this.reg = itemViewer.getRegItemType(ld.itModel);
		this._subArea = this.reg.env.itemType.getSrcMainArea(this);
	}

	getId(): string {
		return this._id;
	}

	getLabel(ctx: ItemViewerMulti): string {
		return this._subArea.getLabel(this);
	}

	getIcon(ctx: ItemViewerMulti): string {
		return this._subArea.getIcon(this);
	}

	getDescription(ctx: ItemViewerMulti): string {
		return this._subArea.getDescription(this);
	}

	getGroup(ctx: ItemViewerMulti): string {
		return this._subArea.getGroup(this);
	}

	getLastDatasKey(ctx: ItemViewerMulti): string {
		return this._subArea.getLastDatasKey(this);
	}

	getSkin(ctx: ItemViewerMulti): string {
		return this._subArea.getSkin(this);
	}

	getSkinOver(ctx: ItemViewerMulti): string {
		return this._subArea.getSkinOver(this);
	}

	isAvailable(ctx: ItemViewerMulti): boolean {return true}

	isEnabled(ctx: ItemViewerMulti): boolean {return true}

	isVisible(ctx: ItemViewerMulti): boolean {return true}

	isInstantiable(ctx: IItemViewer): boolean {return true}

	needAsync(ctx: ItemViewerMulti): boolean {
		return this._subArea.needAsync(this);
	}

	loadBody(ctx: ItemViewerMulti, lastDatas?: JLastDatas): Promise<SrcMain> {
		return this._subArea.loadBody(this, lastDatas);
	}

	loadLibs(ctx: IItemViewer): Promise<void> {
		return this._subArea.loadLibs(this);
	}

	buildBody(ctx: ItemViewerMulti, lastDatas?: JLastDatas): SrcMain {
		return this._subArea.buildBody(this, lastDatas);
	}
}

/** LastDatas dÃ©diÃ©s. */
interface JLDItemViewerMulti extends JLastDatas {
	main?: JLDItemViewerMultiTabs
	left?: JLDItemViewerMultiTabs
	right?: JLDItemViewerMultiTabs
	floatings?: JLastDatas[]
}

interface JLDItemViewerMultiTabs extends JLastDatas {
	tab?: string
	tabs?: JLDItemViewerMultiTab[]
	histo?: string[]
}

interface JLDItemViewerMultiTab extends JLastDatas {
	srcRef: srcRef
	pin?: boolean
}


/** Regroupement d'onglets dÃ©diÃ©s */
export class SrcTabs extends Tabs<ItemViewerMulti> {

	reg: IReg<IWspUiEnv>;

	menuActions: IAction<SrcTab>[];

	histo: Set<srcRef>;

	get tabsPos(): 'left' | 'main' | 'right' {return this.getAttribute('last-datas') as any}

	isMainTabs(): boolean {return this.areasContext.mainTabs === this}

	async selectTab(tab: Tab<ItemViewerMulti>, lastDatas?: JLastDatas): Promise<boolean> {
		if (tab === this._selectedTab) {
			//On force un refocus (scrolls explorateur...)
			const area = tab.area;
			if (area instanceof SrcArea && this.isMainTabs()) {
				//On est dans les onglets principaux et l'onglet affichÃ© est de type item.
				const infoBroker = this.areasContext.reg.env.infoBroker;
				if (infoBroker) infoBroker.dispatchInfo(new InfoCurrentItem(area.shortDesc.srcUri, area.shortDesc), this);
			}
			return true;
		}
		return super.selectTab(tab, lastDatas);
	}

	async closeTab(tab: SrcTab, silently?: boolean): Promise<boolean> {
		if (!await super.closeTab(tab, silently, true)) return false;
		if (!this.isMainTabs() && !this._tabs.firstElementChild) {
			//Tabs secondaires et plus de tabs, on masque
			this.areasContext.hideTabs(this);
		}
		return true;
	}

	getTabBySrcRef(srcRef: srcRef): SrcTab {
		return this.getTabByCode(srcRef) as SrcTab;
	}

	closeItem(srcRef: srcRef) {
		const tab = this.getTabBySrcRef(srcRef);
		if (tab) this.closeTab(tab);
	}

	closeSpace(spaceUri: srcUri) {
		this.visitTabs((t) => {
			const area = t.area;
			if (area instanceof SrcArea) {
				if (SRC.isSubUriOrEqual(spaceUri, area.shortDesc.srcUri)) this.closeTab(t as SrcTab);
			}
		})
	}

	addHisto(srcRef: srcRef) {
		if (this.histo.size > 12) {
			let del = 0;
			for (const s of this.histo) {
				this.histo.delete(s);
				if (++del === 12) break;
			}
		}
		this.histo.add(srcRef);
	}

	remHisto(srcRef: srcRef) {
		this.histo.delete(srcRef);
	}

	getOldestRemovableTabs(): SrcTab[] {
		const result: SrcTab[] = [];
		let tab = this._tabs.firstElementChild;
		while (tab) {
			if (tab instanceof SrcTab && !tab.frozen && !tab.pinned) result.push(tab);
			tab = tab.nextElementSibling;
		}
		result.sort((t1, t2) => t1.showTime - t2.showTime);
		return result; //[this._tabs.firstElementChild as SrcTab]; //TODO
	}

	protected _initialize(init: OTabsInit<ItemViewerMulti>): Promise<void> {
		this._movable = true;
		this._showOnDragOver = true;
		this.classList.add("noBorder");
		this.setAttribute("c-resizable", "");
		this.style.flex = "1";
		init.skin = "c-tabs/wsp-src-tabs";
		init.cbOverflow = async (overflow: boolean) => {
			if (overflow) {
				//Tentavie de cleanup des anciens tabs.
				const oldests = this.getOldestRemovableTabs();
				for (let i = 0, s = oldests.length - 2; i < s; i++) {// -2 : on garde toujours au moins 2 onglets
					const oldest = oldests[i];
					if (oldest.isConnected) {
						await this.closeTab(oldest, true);
						if (!this._tabs.isOverflow) return;
					}
				}
			}
		};
		this.reg = this.findReg(init);

		if (init.lastDatas && (init.lastDatas as JLDItemViewerMultiTabs).histo) this.histo = new Set((init.lastDatas as JLDItemViewerMultiTabs).histo);
		else this.histo = new Set();
		this.append(
			<hr slot="tools-end"/>,
			<Button slot="tools-end" title="Historique des items affichÃ©s" Ã®={{
				reg: this.reg,
				icon: "/@skin@/wsp/views/itemViewerMulti/histo.svg"
			} as OButtonInit} onclick={this.onClickHisto}/>,
			<ActionBtn slot="tools-end" Ã®={{
				reg: this.reg,
				action: new OpenFastFindItem(),
				actionContext: this,
				accel: ACTION.getAccelText({mainKey: "<", accelKey: true}),
				uiContext: 'bar'
			} as OActionBtnInit<any>}/>,
			<ActionBtn slot="tools-end" Ã®={{
				reg: this.reg,
				action: new OpenSearchItems(),
				accel: ACTION.getAccelText({mainKey: "F", accelKey: true, shiftKey: true}),
				actionContext: this,
				uiContext: 'bar'
			} as OActionBtnInit<any>}/>
		);

		this.menuActions = ACTION.injectSepByGroup(this.reg.getList<IAction<SrcTab>>("actions:itemViewerMulti"), "tab * close", null);

		this.addEventListener("wsp-need-valid", this.onNeedValid);

		return super._initialize(init);
	}

	_initShadowRoot() {
		//Tabs en bas
		const sr = this.shadowRoot;
		this._tabs.id = 'tabs';
		sr.appendChild(<div id='head'>
			<slot name='tools-start'/>
			{this._tabs}
			<slot name='tools-end'/>
		</div>);
		sr.appendChild(<div id='panels'>{this._mainSlot}</div>);
	};

	_initTabs() {
		if (this._areas) this._areas.forEach((area: IArea<ItemViewerMulti>) => {
			this._newTab(area);
		});
	}

	_newTab(area: IArea<ItemViewerMulti>, before?: Tab<ItemViewerMulti>): SrcTab {
		const tab = new SrcTab().initialize({
			parentViewCtn: this,
			area: area
		});
		this._initTab(tab);
		this._tabs.insertBefore(tab, before);
		return tab;
	}

	buildLastDatas(p: JLDItemViewerMulti) {
		const k = this.getAttribute("last-datas") as 'main' | 'left' | 'right';
		const ld = {} as JLDItemViewerMultiTabs;
		ld.histo = Array.from(this.histo);
		ld.tabs = [] as JLDItemViewerMultiTab[];
		this.visitTabs((t: SrcTab) => {
			if (!t.frozen && t.area instanceof SrcArea) {
				const s = {} as JLDItemViewerMultiTab;
				if (t.selected) {
					ld.tab = t.code;
					LASTDATAS.buildLastDatas(s, t.view, true);
				} else if (t.area.lastDatas) Object.assign(s, t.area.lastDatas);
				s.srcRef = t.area.getId();
				s.pin = t.pinned ? true : undefined;
				ld.tabs.push(s);
			}
		});
		if (this.isMainTabs()) {
			p[k] = ld;
			this.areasContext.buildLastDatas(p);//floating windows
		} else if (ld.tabs.length > 0) {
			//Onglets secondaires renseignÃ©s que si non vides.
			p[k] = ld;
		}
	}

	async onClickHisto(this: Button, ev: Event) {
		const target = ev.target as HTMLElement; //avant async
		const tabs = this.parentElement as SrcTabs;
		const actions: IAction<SrcTabs>[] = [];
		if (tabs.histo.size > 0) {
			const wsp = tabs.reg.env.wsp;
			const srcs = await WSP.fetchShortDescs(wsp, tabs, ...tabs.histo);
			for (const src of srcs) {
				if (src && src.srcSt > 0 && src.itModel != null) actions.push(new SrcOpenFromHisto(tabs.areasContext, src));
			}
		}
		if (actions.length === 0) actions.push(new Action<SrcTabs>('none').setEnabled(false).setLabel("Historique vide"));
		else actions.reverse();
		actions.push(
			new ActionSeparator(),
			new OpenFastFindItem().setLabel("Autre item...")
		);
		POPUP.showPopupActionsFromEvent({
			reg: tabs.reg,
			actions,
			actionContext: tabs
		}, ev, target);
	}


	protected onNeedValid(ev: CustomEvent) {
		const tab = this.findTabFromDesc(ev.target as Node) as SrcTab;
		if (tab) tab.needValid = ev.detail as boolean;
	}
}


