/** Onglet d'un item. */
export class SrcTab extends Tab<ItemViewerMulti> implements IContextMenuActionsPointer<SrcTab> {

	parentViewCtn: SrcTabs;

	/** Onglet Ã©pinglÃ© par le user. */
	get pinned(): boolean {return this.classList.contains('pinned')};

	set pinned(v: boolean) {this.classList.toggle('pinned', v)};

	/** Onglet dont le contenu attend une action explicite de l'utilisateur de validation. */
	get needValid(): boolean {return this.classList.contains('needValid')};

	set needValid(v: boolean) {this.classList.toggle('needValid', v)};

	/** Onglet figÃ© applicativement. Ne peut Ãªtre supprimÃ© par le user. */
	frozen: boolean;

	showTime: number;

	_bodyElt: HTMLElement;
	_closeElt: HTMLElement;

	isMainTabs(): boolean {return this.areaContext.mainTabs === this.parentViewCtn}

	/** Ne doit Ãªtre appelÃ© que par SrcTabs. */
	async closeTabView(silently?: boolean): Promise<boolean> {
		if (this.area instanceof SrcArea) this.parentViewCtn.addHisto(SRC.srcRef(this.area.shortDesc));
		if (!await super.closeTabView(silently)) return false;
		if (this.selected && this.isMainTabs() && this.area instanceof SrcArea) {
			//On est dans les onglets principaux et l'onglet est sel, on dispatch la perte de la sel.
			const infoBroker = this.parentViewCtn.areasContext.reg.env.infoBroker;
			if (infoBroker) infoBroker.dispatchInfo(new InfoCurrentItem(null), this);
		}
		return true;
	}

	async show(lastDatas?: JLastDatas): Promise<void> {
		const area = this.area;
		await super.show(area instanceof SrcArea ? area.lastDatas : lastDatas);
		this.showTime = Date.now();
		if (this.isMainTabs() && area instanceof SrcArea) {
			//On est dans les onglets principaux et l'onglet affichÃ© est de type item.
			const view = this.view as SrcMain;
			if (!view) return;
			await view.initializedAsync;
			area.longDesc = area.shortDesc = view.reg.env.longDesc;
			const infoBroker = this.parentViewCtn.areasContext.reg.env.infoBroker;
			if (infoBroker) infoBroker.dispatchInfo(new InfoCurrentItem(area.shortDesc.srcUri, area.shortDesc), this);
		}
	}

	async hide(willSelect?: Tab<ItemViewerMulti>): Promise<boolean> {
		if (this.area instanceof SrcArea) this.area.lastDatas = LASTDATAS.buildLastDatas({}, this.view, true);
		const hidden = await super.hide();
		if (hidden && willSelect == null && this.isMainTabs() && this.area instanceof SrcArea && this.area.longDesc) {
			//Pas de nouvel onglet Ã  afficher (si willSelect non null, 2 events successifs et problÃ¨me avec l'historique du navigateur)
			//On est dans les onglets principaux et l'onglet affichÃ© est de type item.
			//=> on dispatch l'absence d'onglet sÃ©lectionnÃ©
			const infoBroker = this.parentViewCtn.areasContext.reg.env.infoBroker;
			if (infoBroker) infoBroker.dispatchInfo(new InfoCurrentItem(null), this);
		}
		return hidden;
	}

	updateSrc(shortDesc: JSrcFields, longDesc?: JSrcFields) {
		if (this.area instanceof SrcArea) {
			this.area.shortDesc = shortDesc;
			this.area.longDesc = longDesc;
			this.refresh();
		}
	}

	async rebuildItemView(itemViewer: ItemViewerMulti, longDesc: JSrcFields) {
		if (this.area instanceof SrcArea) {
			this.area.updateSrcAndModel(itemViewer, longDesc);
			const wasShown = this._shown;
			this._shown = false;
			this.needValid = false;
			await super.closeTabView(true);
			if (wasShown) super.show(this.area.lastDatas);
			else this.refresh();
		}
	}

	get ctxMenuActions(): IContextMenuActions<SrcTab> {
		return {
			actions: this.parentViewCtn.menuActions,
			actionContext: this
		}
	}

	protected _initialize(init: OTabInit<ItemViewerMulti>) {
		if (!init.skin) init.skin = "wsp-src-tab";
		super._initialize(init);
		this._bodyElt = this.shadowRoot.appendChild(<div/>);
		this._iconElt = this._bodyElt.appendChild(<span class='icon'/>);
		this._labelElt = this._bodyElt.appendChild(<span class='label'/>);
		this.addEventListener("pointerover", this.onPointerOver); //btn close
		if (init.area instanceof SrcArea) {
			this.parentViewCtn.remHisto(SRC.srcRef(init.area.shortDesc));
			this.pinned = init.area.pinned === true;
			this.showTime = init.area.showTime;
			this.setAttribute("draggable", "true");
			this.addEventListener("dragstart", this._onDragStart);
			this.addEventListener('dragend', ITEM.resetShortDescTransferToDragSession);
			this.addEventListener('pointerenter', function (this: SrcTab, ev) {
				const tt = new PopupTooltip().initialize({
					tooltipOf: this,
					hoverAllowed: true
				});
				tt.addEventListener('c-show', function (this: PopupTooltip, ev) {
					const srcArea = (this.tooltipOf as SrcTab).area as SrcArea;
					const drawer = this.view as SrcDrawer || this.appendChild(new SrcDrawer().initialize({skin: 'wsp-src-drawer/tooltip', tpl: SRCDRAWER.detailsTpl, reg: srcArea.reg}));
					const itemType = srcArea.reg.env.itemType;
					const tpl = itemType ? SRCDRAWER.detailsTpl(itemType, srcArea.shortDesc, drawer) : null;
					if (tpl) renderAppend(tpl, drawer.shadowRoot);
					else this.close();
				});
				tt.forwardPointerEnter(ev);
			}, {once: true});
		} else {
			this.pinned = false;
			this.showTime = 0;
		}
		this.frozen = false;
	}

	protected _onDragStart(ev: DragEvent) {
		if (this.area instanceof SrcArea) {
			ITEM.setShortDescTransferToDragSession({
				reg: this.area.reg,
				infoBroker: this.area.reg.env.infoBroker,
				shortDescs: [this.area.shortDesc],
				emitter: this
			}, ev, 'build');
		}
	}

	/** Affichage du bouton close. */
	protected onPointerOver(ev: PointerEvent) {
		if (!this._closeElt) {
			this.addEventListener("pointerout", this.onPointerOut);
			this._closeElt = this.shadowRoot.appendChild(<ActionBtn id="close" Ã®={{
				action: closeAction,
				actionContext: this,
				uiContext: 'bar'
			} as OActionBtnInit<SrcTab>}/>);
			this._closeElt.onpointerdown = (ev: Event) => {ev.stopPropagation()}//bloque le click et le drag du tab.
		} else {
			DOM.setHidden(this._closeElt, false);
		}
	}

	/** Masquage du bouton close. */
	protected onPointerOut(ev: PointerEvent) {
		if (this._closeElt) DOM.setHidden(this._closeElt, true);
	}

}

