class OpenInTabs extends Action<SrcTab> {
	_id: 'left' | 'main' | 'right';

	constructor(pos: 'left' | 'main' | 'right', label: string) {
		super(pos);
		this._label = label;
		this._group = 'tab';
	}

	isVisible(ctx: SrcTab): boolean {
		return ctx.parentViewCtn.tabsPos !== this._id;
	}

	execute(ctx: SrcTab, ev?: Event): any | "noPreventDefault" {
		ctx.parentViewCtn.areasContext.openSrcRefInTabs(this._id, SRC.srcRef((ctx.area as SrcArea).shortDesc));
	}
}

