/**
 * Action d'affichage d'un src issue de l'histo dans un SrcTabs.
 */
class SrcOpenFromHisto extends Action<SrcTabs> implements ISrcMainCtx {

	reg: IReg<IItemTypeUiEnv>;
	shortDesc: JSrcFields;

	protected _subArea: IArea<ISrcMainCtx, SrcMain>;

	constructor(itemViewer: ItemViewerMulti, shortDesc: JSrcFields) {
		super(null);
		this.shortDesc = shortDesc;
		this.reg = itemViewer.getRegItemType(shortDesc.itModel);
		this._subArea = this.reg.env.itemType.getSrcMainArea(this);
	}

	getLabel(ctx: SrcTabs): string {
		return this._subArea.getLabel(this);
	}

	getIcon(ctx: SrcTabs): string {
		return this._subArea.getIcon(this);
	}

	getDescription(ctx: SrcTabs): string {
		return this._subArea.getDescription(this);
	}

	getGroup(ctx: SrcTabs): string {
		return this._subArea.getGroup(this);
	}

	isEnabled(ctx: SrcTabs): boolean {
		return this._subArea.isEnabled(this);
	}

	isVisible(ctx: SrcTabs): boolean {
		return this._subArea.isVisible(this);
	}

	execute(ctx: SrcTabs, ev?: Event): any | "noPreventDefault" {
		ctx.addTab(new SrcArea(ctx.areasContext, this.shortDesc), {select: true});
	}
}
